.is-inside
	// padding-top: 75px
	.footer
		margin-top: -42px
		+r(800)
			margin-top: 0
.back
	font-weight: 700
	letter-spacing: 3px
	float: right
	+r(800)
		display: block
		text-align: left
		float: none
		margin-bottom: 10px
		text-align: right
	&:hover
		opacity: .5
	a
		text-decoration: none
		font-size: 11px
		text-transform: uppercase
		color: #171717
		i
			+s(arrowright)
			display: inline-block
			position: relative
			top: 2px
			transform: rotate(180deg)
			margin-right: 14px
	&.back_white
		color: #fff
		opacity: .5
		&:hover
			opacity: 1
		a
			color: #fff
			i
				+s(arrowleft)
				transform: rotate(0deg)
.twocols
	column-count: 2
	column-gap: 40px
	+r(800)
		column-count: 1
.onecol
	width: 50%
	+r(800)
		width: auto


.kontakt
	+clr
	position: relative
	min-height: 100vh
	+r(800)
		min-height: 0
		padding: 80px 30px 30px 30px
	.back
		position: absolute
		top: 50px + 75px
		right: 100px
		background: #fff
		z-index: 20
		padding: 3px 10px
		+r(800)
			position: static
			padding: 0
	.kontakt__content
		float: left
		width: 50%
		padding: 118px 80px 80px 94px
		min-height: 80vh
		+r(800)
			padding: 0
			width: auto
			float: none
			min-height: 0
	.kontakt__right
		float: right
		width: 50%
		min-height: 100vh
		position: relative
		+r(800)
			float: none
			width: auto
			min-height: 300px
		#map
			position: absolute
			top: 0
			left: 0
			right: 0
			bottom: 42px
.cenik
	position: relative
	min-height: 100vh
	overflow: hidden
	+r(800)
		min-height: 0
	&:before
		width: 50%
		z-index: -1
		position: absolute
		bottom: 0
		top: 0
		height: 100%
		right: 0
		background: #1fcb73
		content: ' '
		+r(800)
			display: none
	.back.back_white
		position: absolute
		top: 50px + 75px
		right: 100px
		opacity: 1 !important
		+r(800)
			position: absolute
			top: 74px
			right: auto
			left: 30px
			a
				color: #000

			i
				+s(arrowright)
				transform: rotate(180deg)

		&:hover
			opacity: 0.5 !important
	.cenik__right
		width: 50%
		background: #1fcb73
		float: right
		min-height: 80vh
		padding: 200px 80px 80px 94px
		+r(800)
			padding: 30px
			float: none
			width: auto
			min-height: 0
		h3
			text-transform: uppercase
			color: #fff
			font-size: 14px
			letter-spacing: 3px
			font-weight: normal
			margin-bottom: 5px
		p
			color: #000
			font-size: 14px
			margin-bottom: 34px
			strong
				text-transform: uppercase
				font-weight: 700
				letter-spacing: 3px
			a
				color: #000
				text-decoration: underline
	.cenik__content
		float: left
		width: 50%
		min-height: 80vh
		background: #fff
		padding: 125px 100px 100px 94px
		+r(800)
			padding: 100px 30px 30px 30px
			float: none
			width: auto
			min-height: 0

.form
	font-size: 0
	padding-top: 5px
	input[type=text]
		font-size: 14px
		background: #fff
		height: 43px
		padding: 0 10px
		line-height: normal
		width: 50%
		&.is-first
			border-right: 10px solid #1fcb73
		+placeholder
			color: #999999
	textarea
		font-size: 14px
		margin-top: 10px
		color: #999
		padding: 10px 15px
		height: 88px
		display: block
		width: 100%
		border: none
		margin-bottom: 10px
	input[type=submit]
		border: 2px solid #fff
		color: #fff
		background: transparent
		font-weight: 700
		font-size: 14px
		color: #fff
		text-align: center
		width: 50%
		padding: 10px 0
		text-transform: uppercase
		letter-spacing: 2px
		&:hover
			background: #fff
			color: #1fcb73
.lide
	position: relative
	text-align: center
	min-height: 100vh
	padding-top: 60px + 75px
	+r(800)
		padding: 80px 30px 30px 30px
		min-height: 0
	h2
		font-size: 36px
		font-weight: 100
		text-align: center
		margin-bottom: 25px
		letter-spacing: 3px
		+r(800)
			font-style: 24px
	h3
		font-size: 14px
		font-weight: normal
		color: #999999
		letter-spacing: 3px
		text-transform: uppercase
		margin-bottom: 35px
	.back
		top: 50px + 75px
		right: 100px
		position: absolute
		+r(800)
			position: static

.person
	display: inline-block
	margin: 0 60px
	text-align: center
	&__pic
		width: 131px
		height: 131px
		border-radius: 50%
		background-color: #ccc
		background-size: cover
		margin: 0 auto
		margin-bottom: 10px
	&__name
		font-size: 14px
		color: #555555
		font-weight: 700
	&__pos
		font-size: 14px
		font-weight: normal
		margin-top: 0px
		margin-bottom: 10px
	&__phone
		font-weight: 700
		letter-spacing: 1px
		color: #000000
		font-size: 14px
		margin-top: 3px
	a
		color: #000000
		text-decoration: underline
		font-size: 14px

.akce
	background: #212121
	position: relative
	padding-top: 54px + 75px
	padding-bottom: 100px
	padding-left: 100px
	padding-right: 100px
	min-height: 100vh
	+r(800)
		min-height: 0
		padding: 80px 30px 30px 30px
	&.lessitems
		.akce__next,.akce__prev
			display: none
		.akce__slides
			text-align: center
			background: none
			box-shadow: none
			font-size: 0
			min-height: 300px
			height: auto
		.akce__item
			display: inline-block
			width: 300px
			vertical-align: top
			text-align: left
			min-height: 300px
			height: auto
			padding-bottom: 30px
			+r(800)
				width: auto
		.akce__slider
			box-shadow: none
	.back
		top: 50px + 75px
		right: 100px
		position: absolute
		+r(800)
			position: static
	h2
		text-align: center
		font-weight: 100
		color: #fff
		font-size: 36px
		margin-bottom: 43px
	&__info
		text-align: center
		margin-top: 40px
		strong
			text-align: center
			font-size: 14px
			font-weight: normal
			color: #999999
			letter-spacing: 3px
			display: block
			text-transform: uppercase
		span
			display: block
			text-align: center
			margin: 10px 0
			color: #fff
			font-size: 14px
			letter-spacing: 3px
		a
			font-size: 14px
			color: #fff
			text-decoration: underline
	.akce__slider
		position: relative
		box-shadow: 0 3px 20px rgba(0, 0, 0, 0.91)
	&__slides
		background: #fb5181
		height: 300px
		position: relative

	&__prev,&__next
		position: absolute
		top: 50%
		z-index: 20
		margin-top: -10px
		+s(arrowleft)
		cursor: pointer
	&__prev
		left: -40px
	&__next
		right: -40px
		transform: rotate(180deg)
	&__item
		height: 300px
		background: #fb5181
		box-shadow: 0 3px 20px rgba(0, 0, 0, 0.91)
		padding: 40px 40px 0 63px
		font-size: 14px
		color: #fff
		h3
			font-size: 24px
			font-weight: 100
			color: #fff
			letter-spacing: 3px
			margin-bottom: 6px
		.akce__meta
			font-size: 14px
			margin-bottom: 6px
			letter-spacing: 2px
			span
				color: #83203b
		.akce__text
			line-height: 1.4
		&_1
			background: #fb5181
		&_2
			background: #eb4674
		&_3
			background: #de3f6b

.rezervace
	min-height: 100vh
	padding-top: 110px
	+r(800)
		min-height: 0
		padding-top: 80px
.ledpanel
	max-width: 970px
	margin: 0 auto
	padding: 115px 0 100px 0
	min-height: 100vh
	+r(800)
		min-height: 0
		padding: 80px 30px 30px 30px
	h2+h3
		width: 50%
		+r(800)
			width: auto
	.back
		a
			text-decoration: none
.smycka
	position: relative
	min-height: 100vh

	+r(800)
		min-height: auto
		padding: 80px 30px 30px 30px
	.back
		position: absolute
		right: 100px
		top: 50px + 75px
		z-index: 10
		color: #fff
		+r(800)
			position: static
		a
			color: #fff
			+r(800)
				color: #000
			&:hover
				color: #000
			i
				+s(arrowleft)
				transform: rotate(0)
				+r(800)
					+s(arrowright)
					transform: rotate(180deg)
	.smycka__content
		padding: 125px 550px 100px 93px
		+r(800)
			padding: 0
	.smycka__pic
		position: absolute
		right: 0
		top: 0
		bottom: 0
		width: 470px
		min-height: 100vh
		background: url(../img/smycka.png) no-repeat 50% 50%
		background-size: cover
		+r(800)
			display: block
			height: 400px 
			background-size: contain
			width: auto
			position: static
			min-height: 0
			background: url(../img/graphic.png) no-repeat 50% 50%
.text
	font-size: 14px
	font-size: 14px
	color: #555555
	line-height: 1.3
	p
		margin-bottom: 1.1em
		-webkit-column-break-inside: avoid
		page-break-inside: avoid
		break-inside: avoid
		display: inline-block
		width: 100%
	small
		display: block
		color: #999999
		font-size: 12px
	a
		color: #333
		text-decoration: underline
	h3
		font-size: 14px
		text-transform: uppercase
		color: #999999
		margin-bottom: 13px
		letter-spacing: 5px
		font-weight: normal
		display: inline-block
		width: 100%
	ul
		list-style-type: disc
		margin: 0 2em 1em 17px
		-webkit-column-break-inside: avoid
		page-break-inside: avoid
		break-inside: avoid
		display: inline-block
		width: 100%
		+r(800)
			width: auto
			display: block
	li
		-webkit-column-break-inside: avoid
		page-break-inside: avoid
		break-inside: avoid
	h2
		font-weight: 100
		font-size: 36px
		margin-bottom: 26px
		color: #171717
		text-transform: uppercase
		letter-spacing: 5px
